.blog-sidebar-list-wrapper {
    .blog-item-thumb {
        padding: 21px;

        .blog-thumb-img img {
            width: 100%;
            border-radius: 5px;
            @include themed() {
                border: 0.5px solid t('bg-panel');
            }
            box-shadow: 0px 3px 15px rgba(0,0,0,0.1);
        }

        .text-content {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .title {
                @include themed() {
                    color: t("color-text");
                }
                font-size: 1.5em;
            }

            .actions {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-gap: 15px;

                .action-icon {
                    cursor: pointer;
                    font-size: 1.5em;
                    transition: 0.5s ease-in-out;
                    @include themed() {
                        color: t('color-text');
                    }

                    &:hover {
                        color: #FF6961;
                    }
                }
            }
        }
    }
}