.blog-container {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    @include themed() {
        background-color: t('bg');
        background-image: t('bg-panel');
    }

    .content-container {
        display: flex;
        flex-direction: column;
        width: 800px;
        align-items: center;

        .title {
            font-size: 1.5em;
            @include themed() {
                color: t('color-headings');
            }
        }

        .actions {
            display: grid;
            grid-template-columns: 1fr;
            grid-gap: 15px;

            .action-icon {
                cursor: pointer;
                font-size: 1.5em;
                transition: 0.5s ease-in-out;
                @include themed() {
                    color: t('color-text');
                }

                &:hover {
                    color: #FF6961;
                }
            }
        }

        .category {
            @include themed() {
                color: t('color-text');
            }
        }

        .content {
            @include themed() {
                color: t('color-text');
            }
        }

        .content img {
            width: 600px!important;
            border-radius: 5px;
        }

        .featured-image-wrapper {
            img {
                margin: 15px 0px;
                width: 600px;
                height: 600px;
                border-radius: 5px;
                object-fit: contain;
                box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
            }
        }
    }
}

@media (min-width: 48em) and (max-width: 1600px) {
    .blog-container {
        height: 100%;
    }
}

@media (max-width: 1800px) {

    .blog-container {
        height: 100vh;
        width: 100vw;

        .content-container {
            width: auto!important;

            .featured-image-wrapper img {
                width: 300px;
                height: 300px;
            }

            .content {
                margin-left: 20px;
                margin-right: 20px;
            }
        }
    }
    
}