.blog-form-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 21px;
    padding: 42px;
  
    @include input-element();
    input {
        margin-bottom: 0px;
    }

    .image-uploader {
        input {
            border-bottom: none;
        }
        .spin-icon {
            font-size: 1.5em;
            @include themed() {
                color: t("color-headings")
            }
        }

    }

    .blog-manager-image-wrapper {
        img {
            width: 100%;
            height: 120px;
            object-fit: cover;
            border-radius: 5px;
        }

        .image-removal-link {
            display: flex;
            justify-content: center;
            a {
                font-weight: 900;
                color: #FF6961;
                cursor: pointer;
            }
        }
    }
  }